.main-section {
    display: flex;
    flex-direction: column;
    width: 1440px;
    // height: 705px
    margin: 0 auto;
    margin-top: 30px;
    background: {}
    &__search {
        margin: 0 auto;
        margin-bottom: 20px; }
    &__table {
        width: 1400px;
        margin: 0 auto;
        margin-bottom: 20px; }
    &__navigation {
        display: flex;
        justify-content: center;
        margin: 0 auto; } }

input, select {
    width: calc( 100% - 10px );
    background: rgba(255, 255, 255, 0.77);
    border: none;
    outline: none;
    padding: 7px;
    font-size: 13px;
    border: 1px solid rgba(0, 0, 0, 0.3);
    border-radius: 4px;
    box-shadow: inset 0 -5px 45px rgba(192, 192, 192, 0.2), 0 1px 1px rgba(183, 183, 183, 0.471);
    -webkit-transition: box-shadow .5s ease;
    -moz-transition: box-shadow .5s ease;
    -o-transition: box-shadow .5s ease;
    -ms-transition: box-shadow .5s ease;
    transition: box-shadow .5s ease; }

.form-search {
    width: 1400px;
    height: 35px;
    display: flex;
    flex-direction: row;
    align-items: center;
    &__button-box {
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center; } }

.btn-x {
    width: 35px;
    height: 30px;
    border-color: #9a9a9a;
    border-radius: 3px;
    background: #838383;
    color: white;
    font-size: 12px;
    line-height: 8px; }

.btn-search {
    width: 100px;
    height: 35px;
    border-color: #ff7b00;
    border-radius: 5px;
    background: #d88436;
    color: white; }

.btn {
    text-align: center;
    line-height: 8px; }

a {
    color: #b55800;
    text-decoration: underline; }

p {
    margin: 0; }

.readonly {
    background-color: #c5c5c5; }

table {
    width: 1400px;
    margin: 0 auto;
    margin-bottom: 20px; }

small {
    font-size: 15px;
    font-weight: bold; }

th {
    vertical-align: middle;
    text-align: center;
    height: 30px;
    background-color: #b1bac0;
    padding-left: 5px;
    padding-right: 5px; }

td {
    padding: 5px;
    &:nth-child(1) {
        text-align: end; }
    &:nth-child(7) {
        text-align: end; }
    > input, select {
        padding: 3px;
        padding-left: 7px;
        font-size: 16px; } }
td:has( input, select) {
    padding: 1px; }


tr {
    &:nth-child(even) {
        background-color: #b1bac0; } }

.table_buttons {
    height: 24px;
    border-radius: 5px;
    color: white;
    text-align: center;
    font-size: 12px; }

.table__button-box {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    &_edit {
        background-color: #0069af;
        border-color: #0078c8; }
    &_edit:disabled {
        background-color: #4c84aa99;
        border-color: #61abdd99;
        color: #f3f3f3f8; }
    &_dismiss {
        background-color: #d83600;
        border-color: #af2d02; }
    &_dismiss:disabled {
        background-color: #dd663fab;
        border-color: #ff774a9b;
        color: #f3f3f3f8; }
    &_save {
        background-color: #9baf00;
        border-color: #c3dc03; }
    &_cancel {
        background-color: #7b7b7b;
        border-color: #a1a1a1; } }
